// app/videos/VideoCard.jsx
"use client";

import { motion } from "framer-motion";
import Image from "next/image";
import Link from "next/link";

export default function VideoCard({ video }) {
  const { videoId, title, thumbnail, description, link } = video;

  return (
    <motion.article
      whileHover={{ scale: 1.02 }}
      transition={{ duration: 0.25 }}
      className="bg-card border border-border rounded-2xl overflow-hidden shadow-sm"
    >
      {/* Thumbnail clickable — open YouTube in new tab or you can implement a modal */}
      <Link href={link ?? `https://www.youtube.com/watch?v=${videoId}`} target="_blank" rel="noreferrer">
        <div className="relative aspect-video w-full">
          {thumbnail ? (
            <Image
              src={thumbnail}
              alt={title}
              fill
              className="object-cover"
              sizes="(max-width: 768px) 100vw, 50vw"
            />
          ) : (
            <div className="w-full h-full bg-muted flex items-center justify-center">
              <span className="text-muted-foreground">No preview</span>
            </div>
          )}

          {/* Play icon overlay */}
          <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
            <div className="bg-black/40 p-3 rounded-full">
              <svg width="28" height="28" viewBox="0 0 24 24" fill="none" className="text-white">
                <path d="M8 5v14l11-7L8 5z" fill="white" />
              </svg>
            </div>
          </div>
        </div>
      </Link>

      {/* Content */}
      <div className="p-4">
        <h3 className="text-lg font-semibold text-foreground line-clamp-2">{title}</h3>
        <p className="text-muted-foreground mt-2 text-sm line-clamp-2">{description}</p>

        <div className="mt-4 flex items-center justify-between">
          <a
            href={link ?? `https://www.youtube.com/watch?v=${videoId}`}
            target="_blank"
            rel="noreferrer"
            className="text-primary font-medium hover:underline"
          >
            Watch on YouTube →
          </a>

          <span className="text-xs text-muted-foreground">
            {video.published ? new Date(video.published).toLocaleDateString() : ""}
          </span>
        </div>
      </div>
    </motion.article>
  );
}
